#tela-configuracoes{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Cabeçalho */

#tela-configuracoes header {
    padding: 10px;
    padding-bottom: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#tela-configuracoes header h1 {
    margin-top: 10px;
    margin-bottom: 2px;
    text-align: center;
}

#tela-configuracoes header p {
    text-align: justify;
    margin: 10px;
}

/* Formulário */

#tela-configuracoes form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--cor-plano-de-fundo-primaria);
    margin: 20px 10px;
    padding: 18px;
    border-radius: 5px;
    width: 90%;
    border-radius: 15px;
}

#tela-configuracoes form label{
    text-align: center;
    margin-bottom: 5px;
}

#tela-configuracoes form #botoes-formulario {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#tela-configuracoes form #botoes-formulario button {
    width: 100%;
    margin-top: 8px;
}

/* Quando a largura da tela for 740px ou maior */
@media(min-width: 740px){
    #tela-configuracoes form #botoes-formulario {
        flex-direction: row;
    } 

    #tela-configuracoes form #botoes-formulario button:not(:last-child){
        /* Exceto para o último botão */
        margin-right: 5px;
    }

    #tela-configuracoes form {
        width: 600px;
    }
    
}