#tela-chamador {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#tela-chamador select{
    background-color: #fff;
}

/* Cabeçalho */

#tela-chamador header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px;
    padding-bottom: 0px;
}

#tela-chamador header h1 {
    margin-bottom: 4px;
    margin-top: 10px;
}

#tela-chamador header p {
    text-align: justify;
    margin: 10px;
}

/* Formulário */

#tela-chamador form {
    border-width: 3px;
    border-style: solid;
    border-color: var(--cor-sombra-texto-obr);
    background-color: var(--cor-plano-de-fundo-primaria);
    margin: 20px 10px;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 90%;
}

#tela-chamador form label {
    text-align: center;
    margin-bottom: 5px;
}

#tela-chamador form #botoes-formulario {
    width: 100%;
    display: flex;
    flex-direction: column;
}

#tela-chamador form #botoes-formulario button {
    width: 100%;
    margin-top: 8px;
}

#tela-chamador .btn-perigo {
    background-color: var(--cor-perigo);
}

/* Logomarcas */

#tela-chamador #logomarcas {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#tela-chamador #logomarcas img {
    width: 150px;
}

#tela-chamador #logomarcas img:not(:last-child) {
    margin-right: 15px;
}

/* Quando a largura da tela for 740px ou maior */
@media(min-width: 740px){
    #tela-chamador form, #tela-chamador header {
        width: 60%;
    }
    
    #tela-chamador form #botoes-formulario {
        flex-direction: row;
    }

    #tela-chamador form #botoes-formulario button:not(:last-child){
        /* Exceto para o último botão */
        margin-right: 5px;
    }
}