/* Cabeçalho */
#tela-equipes{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#tela-equipes header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    padding: 10px;
    padding-bottom: 0px;
}

#tela-equipes header h1 {
    margin-bottom: 4px;
}

#tela-equipes header p {
    text-align: justify;
    margin-bottom: 2px;
}

/* Filtro */

#tela-equipes #filtro {
    display: flex;
    margin: 10px 10px;
}

#tela-equipes #filtro > select {
    flex: 1;
    margin-right: 5px;
}

/* Tabela */

#tela-equipes table {
    background-color: var(--cor-plano-de-fundo-primaria);
    width: 100vw;
}

#tela-equipes table thead th {
    padding: 10px;
    border: 0px;
}

#tela-equipes table tr td {
    padding: 10px 10px;
    text-align: center;
}

#tela-equipes table tr td:last-child img {
    width: 16px;
}

#tela-equipes table tr td:last-child button {
    margin: 2.5px;
}

#tela-equipes #lista-vazia {
    background-color: var(--cor-plano-de-fundo-primaria);
    text-align: center;
    margin: 20px 10px;
    padding: 10px;
    border-radius: 5px;
}

/* Modal para chamar equipe */

#tela-equipes #modal-chamada #botoes-formulario {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

#tela-equipes #modal-chamada #botoes-formulario button {
    margin: 5px 0px;
}

#tela-equipes #modal-chamada p {
    margin: 10px 0px;
    border: solid 1px var(--cor-bordas);
    padding: 10px;
}

#tela-equipes:first-child{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#tela-equipes #modal-chamada .form-group {
    padding: 0px 5px;
}

/* Quando a largura da tela for 740px ou maior */
@media(min-width: 740px){
    #tela-equipes table {
        margin: 10px;
        border-radius: 5px;
        width: 840px;
    }

    #tela-equipes > header:first-child {
        max-width: 820px;
    }

    #tela-equipes .content {
        width: 840px;
    }
}