@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,100..900;1,100..900&display=swap');

:root {
  --cor-prmaria: #1e7e74;
  --cor-background-gradiente: linear-gradient(#FBDB3D, #E67008);
  --cor-plano-de-fundo-primaria: #00000017;
  --cor-plano-de-fundo-secundaria: #00000021;
  --cor-plano-de-fundo-primaria-escuro: #eee;
  --cor-bordas: #dadada;
  --cor-botao-desativado: #aab1aa;
  --cor-texto-primaria: rgb(255, 255, 255);
  --cor-texto-primaria-claro: #777;
  --cor-secundaria: #3693b0;
  --cor-sucesso: #84C54F;
  --cor-perigo: #d9534f;
  --cor-alerta: #ffd11a;
  --cor-sombra-texto: #403f413c;
  --cor-sombra-texto-obr: #1d8278;
  --cor-sombra-texto-obr-claro: rgba(22, 0, 51, 0.1);
  --cor-texto-obr: #ffffff;
}

* {
  box-sizing: border-box; /* Para que o padding esteja incluso no valor da largura e/ou altura */
  margin: 0;
  padding: 0;
}

div{
  background-color: #ffffffe4;
}

div div{
  background-color: #ffffff00;
}

h1, h2, h3, h4, h5, h6, p, label {
  text-shadow: 1px 1px 0 var(--cor-sombra-texto),
    -1px 1px 0 var(--cor-sombra-texto),
    1px -1px 0 var(--cor-sombra-texto),
    -1px -1px 0 var(--cor-sombra-texto),
    0px 1px 0 var(--cor-sombra-texto),
    0px -1px 0 var(--cor-sombra-texto),
    -1px 0px 0 var(--cor-sombra-texto),
    1px 0px 0 var(--cor-sombra-texto),
    2px 2px 0 var(--cor-sombra-texto),
    -2px 2px 0 var(--cor-sombra-texto),
    2px -2px 0 var(--cor-sombra-texto),
    -2px -2px 0 var(--cor-sombra-texto),
    0px 2px 0 var(--cor-sombra-texto),
    0px -2px 0 var(--cor-sombra-texto),
    -2px 0px 0 var(--cor-sombra-texto),
    2px 0px 0 var(--cor-sombra-texto),
    1px 2px 0 var(--cor-sombra-texto),
    -1px 2px 0 var(--cor-sombra-texto),
    1px -2px 0 var(--cor-sombra-texto),
    -1px -2px 0 var(--cor-sombra-texto),
    2px 1px 0 var(--cor-sombra-texto),
    -2px 1px 0 var(--cor-sombra-texto),
    2px -1px 0 var(--cor-sombra-texto),
    -2px -1px 0 var(--cor-sombra-texto);
}

body {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: var(--cor-texto-primaria);
  /* background-image: linear-gradient(#E67008, #E67008,#FBDB3D); */
  /* background-color: rgb(255, 0, 0); */
  background-image: url("./assets/images/background.png");
  background-size: cover;
}

#root > div:first-child {
  min-height: 100vh;
}

input, select {
  padding: 10px;
  border: none;
  border-radius: 4px;
}

button {
  background-color: var(--cor-sucesso);
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

button:disabled{
  background-color: var(--cor-botao-desativado);
}

.btn-perigo {
  background-color: var(--cor-perigo);
}

.btn-alerta {
  background-color: var(--cor-alerta);
}

.form-group {
  display: flex;
  flex-direction: column;
  margin: 5px 0px;
  width: 100%;
}

.form-group label {
  margin-bottom: 2px;
}

@media(min-width: 740px){
  h1, h2, h3, h4, h5, h6 {
    color: var(--cor-texto-obr);
    font-family: "Libre Franklin";
    text-shadow: -7px 5px 0 var(--cor-sombra-texto-obr),
    1px 1px 0 var(--cor-sombra-texto-obr),
    -1px 1px 0 var(--cor-sombra-texto-obr),
    1px -1px 0 var(--cor-sombra-texto-obr),
    -1px -1px 0 var(--cor-sombra-texto-obr),
    0px 1px 0 var(--cor-sombra-texto-obr),
    0px -1px 0 var(--cor-sombra-texto-obr),
    -1px 0px 0 var(--cor-sombra-texto-obr),
    1px 0px 0 var(--cor-sombra-texto-obr),
    2px 2px 0 var(--cor-sombra-texto-obr),
    -2px 2px 0 var(--cor-sombra-texto-obr),
    2px -2px 0 var(--cor-sombra-texto-obr),
    -2px -2px 0 var(--cor-sombra-texto-obr),
    0px 2px 0 var(--cor-sombra-texto-obr),
    0px -2px 0 var(--cor-sombra-texto-obr),
    -2px 0px 0 var(--cor-sombra-texto-obr),
    2px 0px 0 var(--cor-sombra-texto-obr),
    1px 2px 0 var(--cor-sombra-texto-obr),
    -1px 2px 0 var(--cor-sombra-texto-obr),
    1px -2px 0 var(--cor-sombra-texto-obr),
    -1px -2px 0 var(--cor-sombra-texto-obr),
    2px 1px 0 var(--cor-sombra-texto-obr),
    -2px 1px 0 var(--cor-sombra-texto-obr),
    2px -1px 0 var(--cor-sombra-texto-obr),
    -2px -1px 0 var(--cor-sombra-texto-obr);
  }

  /* Não possui sombra, apenas contorno */
  .texto-lista, p, label {
    color: var(--cor-texto-primaria);
    font-family: "Libre Franklin";
    text-shadow: 
    -1px 1px 0 var(--cor-sombra-texto-obr-claro),
    1px -1px 0 var(--cor-sombra-texto-obr-claro),
    -1px -1px 0 var(--cor-sombra-texto-obr-claro),
    0px 1px 0 var(--cor-sombra-texto-obr-claro),
    0px -1px 0 var(--cor-sombra-texto-obr-claro),
    -1px 0px 0 var(--cor-sombra-texto-obr-claro),
    1px 0px 0 var(--cor-sombra-texto-obr-claro),
    2px 2px 0 var(--cor-sombra-texto-obr-claro),
    -2px 2px 0 var(--cor-sombra-texto-obr-claro),
    2px -2px 0 var(--cor-sombra-texto-obr-claro),
    -2px -2px 0 var(--cor-sombra-texto-obr-claro),
    0px 2px 0 var(--cor-sombra-texto-obr-claro),
    0px -2px 0 var(--cor-sombra-texto-obr-claro),
    -2px 0px 0 var(--cor-sombra-texto-obr-claro),
    2px 0px 0 var(--cor-sombra-texto-obr-claro),
    1px 2px 0 var(--cor-sombra-texto-obr-claro),
    -1px 2px 0 var(--cor-sombra-texto-obr-claro),
    1px -2px 0 var(--cor-sombra-texto-obr-claro),
    -1px -2px 0 var(--cor-sombra-texto-obr-claro),
    2px 1px 0 var(--cor-sombra-texto-obr-claro),
    -2px 1px 0 var(--cor-sombra-texto-obr-claro),
    2px -1px 0 var(--cor-sombra-texto-obr-claro),
    -2px -1px 0 var(--cor-sombra-texto-obr-claro);
  }
}