#tela-login{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Cabeçalho */

#tela-login header {
    padding: 10px;
    padding-bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#tela-login .mascote {
    width: 250px;
    margin-right: 40px;
}

#tela-login header h1 {
    margin-bottom: 2px;
}

#tela-login header p {
    text-align: center;
    font-size: 1.4em;
}

/* Formulário */

#tela-login form {
    border-width: 3px;
    border-style: solid;
    border-color: var(--cor-sombra-texto-obr);
    background-color: var(--cor-plano-de-fundo-primaria);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px 0px;
    padding: 10px;
    border-radius: 10px;
    width: 95%;
}

#tela-login form label {

    text-align: center;
    font-weight: bold;
    margin-bottom: 5px;
}

#tela-login form #botoes-formulario  {

    width: 100%
}

#tela-login form #botoes-formulario button {
    margin-top: 8px;
    font-weight: bold;
    width: 100%
}

/* Logomarcas */

#tela-login #logomarcas {
    display: flex;
    flex-direction: row;
    justify-content: center;
}


#tela-login #logomarcas img {
    width: 150px;
}

#tela-login #logomarcas img:not(:last-child) {
    margin-right: 15px;
}

@media(min-width: 740px){
    #tela-login form {
        width: 300px;
    }
}