@font-face {
    font-family: 'StateWide';
    src: local("StateWide"), url("../../assets/fonts/StateWide-Bold.ttf");
}

#tela-apresentacao {
    /* background-image: linear-gradient(#e67008, #FBDB3D); */
    overflow: scroll;
}

/* Oculta as barras quando a animação sai da tela */
::-webkit-scrollbar{
    display: none;
}



/* Cabeçalho */

#tela-apresentacao header h1 {
    padding: 20px 0px;
}

#tela-apresentacao #mascote{
    position: absolute;
    width: 250px;
    top: 50px;
    left: 50px;
    visibility: hidden;
}

#tela-apresentacao #mascote-animacao{
    position: absolute;
    width: 1500px;
    top: 50px;
    left: 50px;
    visibility: hidden;
    margin-left: 100%;
    margin-top: 20%;
    transition: all 1.2s ease-in;
}

#tela-apresentacao #equipes-chamadas h2, h1 {
    font-size: 25px;
    text-align: center;
}

/* Equipes */

#tela-apresentacao main {
    margin: 20px 0px;
}

/* Equipe chamada */

#tela-apresentacao main #equipes-chamadas .container-equipe {
    border-width: 5px;
    border-style: solid;
    border-color: var(--cor-sombra-texto-obr);
    /* https://stackoverflow.com/questions/54273702/create-react-blink-color-component */
    -moz-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;

    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: var(--cor-plano-de-fundo-primaria);
    box-shadow: 0px 0px 3px white;
    margin-bottom: 10px;
    padding: 10px 0px 10px 0px;
}

#tela-apresentacao main  #container-ultimas-chamadas{
    border-width: 5px;
    border-style: solid;
    border-color: var(--cor-sombra-texto-obr);
    /* https://stackoverflow.com/questions/54273702/create-react-blink-color-component */
    -moz-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    background-color: var(--cor-plano-de-fundo-primaria);
    box-shadow: 0px 0px 3px white;
    margin-bottom: 10px;
    padding: 10px 0px 10px 0px;
}

#tela-apresentacao main  #container-ultimas-chamadas table td{
    padding: 10px 0;
    font-size: 30px;
    text-align: center;
}

#tela-apresentacao main  #container-ultimas-chamadas .linha-ultimas-chamadas{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    width: 100%;
}

#tela-apresentacao main #equipes-chamadas .animacao-piscar {
    background-color: var(--cor-primaria);
}

#tela-apresentacao main #equipes-chamadas .container-equipe h1 {
    font-size: 60px;
    text-align: end;
}

/* Próximas equipes */

#tela-apresentacao main #proximas-equipes .container-lista {
    background-color: var(--cor-plano-de-fundo-primaria);
    border-width: 5px;
    border-style: solid;
    border-color: var(--cor-sombra-texto-obr);
    box-shadow: 0px 0px 3px white;
    margin-bottom: 10px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#tela-apresentacao main #proximas-equipes .container-lista table tbody tr td {
    font-size: 20px;
}

#tela-apresentacao main #proximas-equipes .container-lista table tbody tr td:not(:last-child) {
    padding-right: 15px;
}

/* Imagens */

#tela-apresentacao #logomarcas {
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
}

#tela-apresentacao #logomarcas img {
    width: 200px;
}

#tela-apresentacao #logomarcas img:not(:last-child) {
    margin-right: 20px;
}

/* Quando a largura da tela for 740px ou maior */
@media(min-width: 740px){

    #tela-apresentacao #mascote{
        visibility: visible;
    }

    #tela-apresentacao #mascote-animacao{
        visibility: visible;
    }

    #tela-apresentacao #texto-nivel{
        margin-bottom: 20px;
    }

    #tela-apresentacao p {
        color: var(--cor-texto-obr);
        font-family: "Libre Franklin";
        text-shadow: 
        -2px -2px 0 var(--cor-sombra-texto-obr),
        0px 2px 0 var(--cor-sombra-texto-obr),
        0px -2px 0 var(--cor-sombra-texto-obr),
        -2px 0px 0 var(--cor-sombra-texto-obr),
        2px 0px 0 var(--cor-sombra-texto-obr),
        1px 2px 0 var(--cor-sombra-texto-obr),
        -1px 2px 0 var(--cor-sombra-texto-obr),
        1px -2px 0 var(--cor-sombra-texto-obr),
        -1px -2px 0 var(--cor-sombra-texto-obr),
        2px 1px 0 var(--cor-sombra-texto-obr),
        -2px 1px 0 var(--cor-sombra-texto-obr),
        2px -1px 0 var(--cor-sombra-texto-obr),
        -2px -1px 0 var(--cor-sombra-texto-obr);

    } 

    #tela-apresentacao h1, #tela-apresentacao h2, #tela-apresentacao h3,#tela-apresentacao h4,#tela-apresentacao h5,#tela-apresentacao h6,#tela-apresentacao .texto-nivel{
        color: var(--cor-texto-obr);
        font-family: "Libre Franklin";
        text-shadow: -7px 5px 0 var(--cor-sombra-texto-obr),
        1px 1px 0 var(--cor-sombra-texto-obr),
        -1px 1px 0 var(--cor-sombra-texto-obr),
        1px -1px 0 var(--cor-sombra-texto-obr),
        -1px -1px 0 var(--cor-sombra-texto-obr),
        0px 1px 0 var(--cor-sombra-texto-obr),
        0px -1px 0 var(--cor-sombra-texto-obr),
        -1px 0px 0 var(--cor-sombra-texto-obr),
        1px 0px 0 var(--cor-sombra-texto-obr),
        2px 2px 0 var(--cor-sombra-texto-obr),
        -2px 2px 0 var(--cor-sombra-texto-obr),
        2px -2px 0 var(--cor-sombra-texto-obr),
        -2px -2px 0 var(--cor-sombra-texto-obr),
        0px 2px 0 var(--cor-sombra-texto-obr),
        0px -2px 0 var(--cor-sombra-texto-obr),
        -2px 0px 0 var(--cor-sombra-texto-obr),
        2px 0px 0 var(--cor-sombra-texto-obr),
        1px 2px 0 var(--cor-sombra-texto-obr),
        -1px 2px 0 var(--cor-sombra-texto-obr),
        1px -2px 0 var(--cor-sombra-texto-obr),
        -1px -2px 0 var(--cor-sombra-texto-obr),
        2px 1px 0 var(--cor-sombra-texto-obr),
        -2px 1px 0 var(--cor-sombra-texto-obr),
        2px -1px 0 var(--cor-sombra-texto-obr),
        -2px -1px 0 var(--cor-sombra-texto-obr);
    }

    /* Equipes */

    #tela-apresentacao main {
        margin-left: 5%;
        margin-right: 5%;
    }

    /* Equipes chamadas */

    #tela-apresentacao main #equipes-chamadas .container-equipe {
        border-radius: 15px;
    }

    #tela-apresentacao main #equipes-chamadas .container-equipe h2:last-child{
        font-size: 40px;
    }
    
    /* Próximas equipes */

    #tela-apresentacao main #proximas-equipes {
        display: flex;
        flex-direction: row;
    }

    #tela-apresentacao main #proximas-equipes .container-lista {
        flex: 1;
        border-radius: 15px;
    }

    #tela-apresentacao main #proximas-equipes .container-lista:not(:last-child) {
        margin-right: 10px;
    }

    #tela-apresentacao main #proximas-equipes .container-lista table tbody tr td {
        font-size: 30px;
    }
}